import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';
import api from '../Api';

export function DeleteMemoryIcon() {
  return (
    <Box sx={{ alignItems: 'center' }}>
      <IconButton
        aria-label="delete"
        sx={{
          bgcolor: 'form.input',
        }}
      >
        <DeleteIcon />
      </IconButton>
      Delete
    </Box>
  );
}

export default async function deleteMemory(bucket, authURL, memoryID, memoryDeleteCallback) {
  try {
    const deleteResults = await api.deleteMemory(bucket, authURL, memoryID);
    memoryDeleteCallback(deleteResults);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}
